var util = require('../../utils/util.js');
var api = require('../../config/api.js');
const pay = require('../../services/pay.js');
var app = getApp();
export default {
	name: '',
	data() {
		return {
			userInfo: {},
			order: {},
			amount: ''
		}	
	},
	onLoad() {
		
	},
	// 页面显示
	onShow() {
		this.getInfo()
	},
	// 页面分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/index/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(e){
		setTimeout(function () {
			wx.stopPullDownRefresh();
		}, 1000);
	},
	methods:{
		//获取个人中心信息
		getInfo() {
			util.request(api.getUserInfo,{},'get').then(res=>{
				if(res.code == 0) {
					this.userInfo = res.data.user_id;
					this.order = res.data.order;
					this.getShareTicket()
				}
			})
		},
		money(e) {
			this.amount = e.detail.value;
		},
		rechargeBtn() {
			if(this.amount == "") {
				wx.showToast({
					icon: 'none',
					title: '金额不能为空'
				})
				return false;
			}else if(!this.sDigit(this.amount)) {
				wx.showToast({
					icon: 'none',
					title: '请输入正确的金额'
				})
				return false;
			}
			
			wx.showLoading({
				mask: true,
				title: '提交中...'
			})
			util.request(api.orderRecharge,{amount: this.amount},'post').then(res=>{
				if(res.code == 0) {
					this.getPayInfo(res.data.id)
				}else {
					wx.hideLoading();
					util.showErrorToast(res.msg)
				}
			})
			
		},
		//获取支付签名数据
		getPayInfo(id) {
			util.request(api.getPayInfo,{id: id,type: 1},'post').then(res=>{
				if(res.code == 0) {
					wx.hideLoading();
				}else {
					wx.hideLoading();
					// wx.reLaunch({
					// 	url: '/pages/pay/result?status=0'
					// })
				}
			})
		},
		sDigit: function (value) {
			var patrn = /^([1-9]\d*|0)(\.\d*[1-9])?$/;
			if (!patrn.exec(value)) {
		        return false;
		    }else {
		        return true;
		    }
		},
		//获取分享数据得到的群信息
		getShareTicket() {
			let ticket = wx.getStorageSync('ticket');
			let pages = getCurrentPages();
			let { options } = pages.pop();
			var that = this;
			if(ticket && options.code != "") {
				wx.getShareInfo({
					shareTicket: ticket,
					success:function(res) {
						let params = {
							code: options.code ? options.code : "",
							iv: res.iv,
							data: res.encryptedData
						}	
						console.log(params)
						that.getShareInfos(params)
					}
				})
			}
		},
		
		//新增分享记录
		getShareInfos(params) {
			util.request(api.setShareRechge, params,'post').then(res=>{
				console.log(res)
			})
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
		
	}
}
